td,
th {
  vertical-align: middle !important;
  text-align: center !important;
}

form {
  margin: 20px 0;
}

input[type='text'] {
  width: 80%;
}

button {
  width: 20%;
}

svg {
  height: 150px;
}

td:first-of-type,
td:first-of-type > div {
  height: 200px;
  width: 250px;
}

body {
  width: 90%;
  margin: 0 auto;
}



